.post ol, .footer__list, .header__list {
  list-style-type: none; }

ul,
ol {
  margin: 0;
  padding: 0; }

.post ol,
.post ul {
  margin-bottom: 1.5rem;
  margin-left: 1em;
  font-size: 1.1em; }
  @media (min-width: 32em) {
    .post ol,
    .post ul {
      font-size: 1.25em; } }
  .post ol > li,
  .post ul > li {
    position: relative; }
    .post ol > li:before,
    .post ul > li:before {
      position: absolute;
      left: -1em; }
    .post ol > li:not(:last-child),
    .post ul > li:not(:last-child) {
      margin-bottom: 0.25rem; }
    .post ol > li p,
    .post ul > li p {
      margin-bottom: 0;
